        <div id="1" class="function-content-wrap">
          <div class="title">Gameplay: </div>
          <div class="paragraph">Rock-paper-scissors is a memorable childhood game for many people. The gameplay is fun, while for most players this game is strong for randomness. However, since the theme of this gamejam is "trick", we attempt to add elements of calculation and deception to make rock-paper-scissors more competitive.
            Since the game is set in a showdown between two "big goose gunslingers" in the West, we replaced the rock-scissors-paper with pan-gun-lasso. The pan beats the gun, the gun beats the lasso, and the lasso beats the pan.
          </div>
          <img src="../../../assets/images/games_detail/3/2_1.jpg"/>
          <div class="paragraph">Each game consists of four rounds, during which the two players must switch weapons in their left and right hands on odd and even rounds respectively. After the four rounds, the players will discard one hand each and engage in a final duel.</div>
          <img src="../../../assets/images/games_detail/3/2_2.jpg"/>
          <div class="paragraph">In addition, we limit the number of each type of weapon. Players must snatch at weapons from a shared pool during each round. Players will affect the weapons available in the pool based on their strategies and keep the weapons they need for themselves. </div>
          <img src="../../../assets/images/games_detail/3/2_3.jpg"/>
          <div class="paragraph">After switching weapons, the players will begin to move one unit in a nine-box map. Moving onto a green tile adds a corresponding weapon while moving onto a red tile reduces a corresponding weapon. Therefore, players will move based on their needs. </div>
          <img src="../../../assets/images/games_detail/3/2_4.jpg"/>
          <div class="paragraph">The core gameplay is a "walking | changing weapon" cycle. At the beginning of each round, the players change one weapon together, then move one step together.
            Using these mechanisms, we enhance the competitive nature of rock-paper-scissors. All four rounds have a time limit, and the game will gradually shorten with each round. The two players must think about how to change weapons and influence the pool in a limited time to outwit and deceive each other.
          </div>
        </div>
        <div id="2" class="function-content-wrap">
          <div class="title">Artwork - Visual and Audio: </div>
          <div class="paragraph">In this game, we used a combination of 2D characters and 3D environments to create the visuals. This combination of 2D and 3D is a style I really enjoy and have been experimenting with.

            For the audio, we incorporated guitar sounds to make a feeling of the West. Besides, we added timed sound effects to increase the tension.

